import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Online from './Online';
import ChatScreen from './ChatScreen';
import ToUser from './ToUser';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import windowDimensions from 'react-window-dimensions';
  
let chatStore = [];
let tos = [];

const ChatBox = ({width,height,user,online,connection,updateOnline})=>{

    const [onlineOpen,setOnlineOpen] = useState(false);

    const [to,setTo] = useState({user:null,innerHTML:''});

    const useStyles = makeStyles(theme => ({
        root: {
          flexGrow: 1,
          margin: 20
        },
          input: {
            marginLeft: theme.spacing(1),
            flex: 1,
          },
          iconButton: {
            padding: 10,
          },
          divider: {
            height: 28,
            margin: 4,
          }
      }));

    const classes = useStyles();

    const storeCurrent = ()=>{
      if(to.user){
        const toIndex = tos.findIndex(e=>(e.user === to.user));
        if(toIndex >= 0){
          for(let i=0;i<tos.length;i++){
            if(tos[i].user === to.user){
              tos[i].innerHTML = document.getElementById(to.user).innerHTML;
            }
          }
        }else{
          tos.push({user:to.user,innerHTML:document.getElementById(to.user).innerHTML});
        }
        
      }
    }

    const addTo = (add)=>{
      const toIndex = tos.findIndex(to=>(to.user === add));
      if(toIndex < 0){
        tos.push({user:add,innerHTML:''});
        setTo({user:add,innerHTML:''});
        setTimeout(()=>pushMessage(add),1000);
      }else{
        setTo(tos[toIndex]);
      }
      storeCurrent();
    }

    useEffect(()=>{
      if(to.user){
        document.getElementById(to.user).innerHTML = to.innerHTML;
      }
    },[to])

    const pushMessage = (fromUser)=>{
      let userIndex = chatStore.findIndex((e)=>(e.user === fromUser));
      if(userIndex >= 0){
        chatStore[userIndex].unreadMessages.forEach((msg)=>(
          addToChat(msg,chatStore[userIndex].user,'left')
        ));
        chatStore.splice(userIndex,1);
        let onlineUsers = [];
        Object.assign(onlineUsers,online);
        for(let i=0;i<onlineUsers.length;i++){
          if(onlineUsers[i].user === fromUser){
            onlineUsers[i].unread = false;
            onlineUsers[i].unreadMessages = [];
          }
        }
        updateOnline(onlineUsers);
      } 
    }

    const sendMessage = (message,to)=>{
      addToChat(message,to,'right');
      connection.send(JSON.stringify({from:user,to:to,message:message}));
    }

    const addToChat = (message,from,side)=>{
        let bgColor = (side==='right')?'white':'#b3ecff';  
        let style = `background-color:${bgColor};border-radius:4px;text-align:left;width:auto;max-width:80%;word-wrap:normal;float:${side};`;
        let displayMsg = `<div style=${style}><span style='margin-left:5px;margin-right:5px'>${message}</span></div><br/>`;
        document.getElementById(from).innerHTML += displayMsg;
    }

    connection.onmessage = (message)=>{
      let incoming = JSON.parse(message.data);
      if(incoming.message){
        if(document.getElementById(incoming.from)){
          addToChat(incoming.message,incoming.from,'left');
        }else{
          let userIndex = chatStore.findIndex(e=>(e.user === incoming.from));
          if(userIndex >= 0){
            chatStore[userIndex].unread = true;
            chatStore[userIndex].unreadMessages.push(incoming.message);
          }else{
            chatStore.push({user:incoming.from,unread:true,unreadMessages:[incoming.message]});
          }
          if(onlineOpen){
            onlineBar(onlineOpen);
          }
        }
      }
      if(incoming.error){
        addToChat(`<i>${incoming.error}</i>`,incoming.to,'left');
      }
      if(incoming.existing){
        let current = [];
        incoming.existing.forEach(e=>{
            if(e!==user){
              current.push({user:e})
            }
        });
        updateOnline(current);
      }
    }

    const onlineBar = (isOpen)=>{
      let onlineUsers = [...chatStore];
      online.forEach(e=>{
        let index = onlineUsers.findIndex(u=>u.user === e.user);
        if(index<0){
          onlineUsers.push(e);
        }
      })
      updateOnline(onlineUsers);
      setOnlineOpen(isOpen);
    }

    /* const removeTo = (user)=>{
      let currentTos = [];
      currentTos = tos.filter(to=>(to!==user));
      tos = currentTos;
    } */

    return (
        <div className={classes.root}>
          <Online user={user} isOpen={onlineBar} addTo={(add)=>addTo(add)} online={online} />
          { to.user && 
              <Box width={onlineOpen?"80%":"100%"} bgcolor="grey.300" p={1} my={0.5} >
                <div>
                  <ToUser to={to.user} />
                  <Divider/>
                  <div style={{maxHeight:height/3,overflowY:"auto"}} id={to.user}>
                      
                  </div>
                  <div style={{marginTop:'20px'}} >
                      <ChatScreen to={to.user} from={user} send={sendMessage} ></ChatScreen>
                  </div>
                </div>
              </Box>              
          } 
        </div>
    );  
}

export default windowDimensions()(ChatBox);

